import express from 'express';
import fs from 'fs/promises';
import { fileURLToPath } from 'url';
import path from 'path';

const __filename = fileURLToPath(import.meta.url);
const __dirname = path.dirname(__filename);

const router = express.Router();
const expensesFilePath = path.join(__dirname, '../data/expenses.json');

// Get all expenses
router.get('/', async (req, res) => {
  try {
    const data = await fs.readFile(expensesFilePath, 'utf8');
    const expenses = JSON.parse(data);
    res.json(expenses);
  } catch (error) {
    console.error('Error reading expenses file:', error);
    res.status(500).json({ error: 'Failed to read expenses data' });
  }
});

// Get expenses by category
router.get('/category/:category', async (req, res) => {
  try {
    const data = await fs.readFile(expensesFilePath, 'utf8');
    const expenses = JSON.parse(data);
    const filteredExpenses = expenses.filter(expense => 
      expense.category.toLowerCase() === req.params.category.toLowerCase()
    );
    res.json(filteredExpenses);
  } catch (error) {
    console.error('Error reading expenses file:', error);
    res.status(500).json({ error: 'Failed to read expenses data' });
  }
});

// Add new expense
router.post('/', async (req, res) => {
  try {
    const data = await fs.readFile(expensesFilePath, 'utf8');
    const expenses = JSON.parse(data);
    
    const newExpense = {
      id: `EXP-${String(expenses.length + 1).padStart(3, '0')}`,
      ...req.body,
      date: new Date().toISOString().split('T')[0]
    };
    
    expenses.push(newExpense);
    
    await fs.writeFile(expensesFilePath, JSON.stringify(expenses, null, 2));
    res.status(201).json(newExpense);
  } catch (error) {
    console.error('Error adding expense:', error);
    res.status(500).json({ error: 'Failed to add expense' });
  }
});

// Update expense
router.put('/:id', async (req, res) => {
  try {
    const data = await fs.readFile(expensesFilePath, 'utf8');
    const expenses = JSON.parse(data);
    
    const expenseIndex = expenses.findIndex(expense => expense.id === req.params.id);
    if (expenseIndex === -1) {
      return res.status(404).json({ error: 'Expense not found' });
    }
    
    expenses[expenseIndex] = { ...expenses[expenseIndex], ...req.body };
    
    await fs.writeFile(expensesFilePath, JSON.stringify(expenses, null, 2));
    res.json(expenses[expenseIndex]);
  } catch (error) {
    console.error('Error updating expense:', error);
    res.status(500).json({ error: 'Failed to update expense' });
  }
});

// Delete expense
router.delete('/:id', async (req, res) => {
  try {
    const data = await fs.readFile(expensesFilePath, 'utf8');
    const expenses = JSON.parse(data);
    
    const filteredExpenses = expenses.filter(expense => expense.id !== req.params.id);
    
    if (filteredExpenses.length === expenses.length) {
      return res.status(404).json({ error: 'Expense not found' });
    }
    
    await fs.writeFile(expensesFilePath, JSON.stringify(filteredExpenses, null, 2));
    res.json({ message: 'Expense deleted successfully' });
  } catch (error) {
    console.error('Error deleting expense:', error);
    res.status(500).json({ error: 'Failed to delete expense' });
  }
});

export default router;
